<template>
  <form @submit.prevent="procesarFormulario">
    <div class="about">
      <h1>Encuesta</h1>
    </div>
    <div>
      <input
        type="text"
        class="form-control my2"
        placeholder="Ingrese Nombre de socio"
        v-model="cuestionario.nombre"
      />
    </div>

    <div class="mt-2">
      <br>
      talla de polera <br>
      <div class="form-check form-check-inline">
        <input 
          type="radio"
          id="radio-1"
          class="form-check-input"
          value="M"
          v-model="cuestionario.talla"
        >
        <label for="radio-1" class="form-check-label">M</label>
      </div>
      <div class="form-check form-check-inline">
        <input 
          type="radio"
          id="radio-2"
          class="form-check-input"
          value="G"
          v-model="cuestionario.talla"
        >
        <label 
        for="radio-2" 
        class="form-check-label">G</label>
      </div>
    </div>
    <button
      class="btn btn-dark mt-2 btn-block"
      type="submit"
      :disabled="bloquear"
    >
      enviar
    </button>
  </form>

  <hr />
  <p>
    {{ cuestionario }}
  </p>
</template>
<script>
export default {
  data() {
    return {
      cuestionario: {
        nombre: "",
      },
    };
  },
  methods: {
    procesarFormulario() {
      console.log(this.cuestionario);
      if (this.cuestionario.nombre.trim() === "") {
        console.log("Campo vacío");
        return;
      }
      console.log("no está vacio");

      // envian los datos

      this.cuestionario = {
        nombre: ""
      };
    },
  },
  computed: {
    //propiedades computadas para boton
    bloquear() {
      return this.cuestionario.nombre.trim() === "" ? true : false;
    },
  },
};
</script>